}

html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__product-name {
  letter-spacing: 0;
  text-align: left;
  color: #060b35;
  width: 166px;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__revies-conteiner {
  justify-content: space-between;
  align-items: flex-end;
  width: 166px;
  margin-bottom: 24px;
  display: flex;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card__revies-count {
  text-align: right;
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

.card__price-conteiner {
  justify-content: space-between;
  width: 166px;
  margin-bottom: 16px;
  display: flex;
}

.card__price-lable {
  color: #616070;
  letter-spacing: 0;
  text-align: left;
  width: 31px;
  height: 18px;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price {
  color: #060b35;
  letter-spacing: 0;
  text-align: right;
  width: 50px;
  height: 18px;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__but {
  text-align: center;
  box-sizing: border-box;
  letter-spacing: 0;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
